* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    text-align: center;
    position: relative;
}

thead {
    display: table-header-group;
    position: sticky;
    top: 0;
    z-index: 10;
}

thead th {
    background-color: #d9eaf1;
    border-bottom: 1px solid #e0e0e0;
    border-right: 1px solid white;
    padding: 10px;
    font-size: 18px;
}

tbody tr {
    cursor: pointer;
}

tbody tr:hover {
    background-color: #eee;
}

tr:nth-child(2n) {
    background-color: #f5f5f5;
}

tr:nth-child(2n) {
    background-color: #f5f5f5;
}

tr {
    border-bottom: 1px solid #e0e0e0;
}

td {
    padding-top: 5px;
    padding-bottom: 20px;
}

td input {
    width: 60px;
    height: 100%;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
}

html,
body {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

body {
    overflow: hidden scroll;
}

:root {
    --bg-gray: #424242;
    --bg-black: #000000;
    --bg-white: #ffffff;
    --bg-green: #02b002;
    --black: #000000;
    --white: #ffffff;
    --white-transparent: rgba(255, 255, 255, 0.8);
    --gray: #424242;
    --box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    --blue: #3a589d;
    --red: #e53935;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

.heading {
    font-size: 38px;
    font-weight: bold;
    color: var(--black);
    text-align: center;
    margin-bottom: 20px;
    border-bottom: 2px solid var(--bg-gray);
    line-height: 68px;
}

.btn {
    display: inline-block;
    border: 1px solid;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1);
    color: var(--white);
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 14px;
    transition: all 0.2s linear;
    cursor: pointer;
}

.btn-green {
    background-color: var(--bg-green);
    color: var(--white);
}

.btn-primary {
    background-color: var(--blue);
    border-color: var(--blue);
}

.btn-danger {
    background-color: var(--red);
    border-color: var(--red);
}

.btn-login,
.btn-register,
.btn-cart {
    background-color: var(--black);
}

.product-container {
    position: relative;
}

.product-add {
    width: 100%;
    max-width: 470px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    box-shadow: var(--box-shadow);
    color: var(--white);
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 14px;
    transition: all 0.2s linear;
    cursor: pointer;
    z-index: 100;
}

.product-add-container {
    position: relative;
}

.product-add-title {
    margin-top: 5px;
    font-size: 18px;
    font-weight: bold;
    color: var(--black);
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.product-add-title h1 {
    margin: 0;
    font-size: 22px;
    padding: 5px;
}

.product-add-title button {
    cursor: pointer;
}

.product-add-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
}

.product-add-form-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
}

.product-add-form-title {
    font-size: 18px;
    font-weight: bold;
    color: var(--black);
    margin-bottom: 10px;
}

.product-add-form-input {
    gap: 20px;
}

.product-add-form-input label {
    font-size: 14px;
    font-weight: bold;
    color: var(--black);
    margin-bottom: 10px;
    margin-right: 10px;
}

.product-add-form-input img {
    height: 40px;
}

.product-add-form-input #product-id {
    background-color: #ccc;
}

.product-add-form-input input {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 14px;
    transition: all 0.2s linear;
    cursor: pointer;
}

.product-add-form-input input[type='file'] {
    margin-top: 5px;
    cursor: pointer;
}

.product-add-form-input textarea {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 14px;
    transition: all 0.2s linear;
    cursor: pointer;
}

.product-add-form-button {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
}

#product-body-list {
    display: contents;
}

.product-table {
    height: 60vh;
    overflow-y: scroll;
}

.product-header {
    position: relative;
}

.product-header .heading {
    text-align: start;
}

.product-header button {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.product-table-img {
    vertical-align: middle;
}

/* dashboard */
.dashboard-container {
    max-width: 1080px;
    margin: 0 auto;
    margin-bottom: 80px;
    padding: 0 20px;
}

.dashboard-content {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 20px;
}

.dashboard-title h1 {
    text-align: start;
}

.dashboard-content-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    justify-content: space-between;
    box-shadow: var(--box-shadow);
}

.dashboard-content-item h3 {
    padding: 10px;
    background-color: var(--bg-gray);
    width: 100%;
    font-size: 18px;
    font-weight: bold;
    color: var(--white);
    margin-bottom: 10px;
}

.dashboard-content-item span {
    font-size: 18px;
    padding: 5px;
    color: var(--gray);
    margin-bottom: 10px;
}

